:root {
  --color-darkCyan: #0e8784;
  --color-darkGreyBlue: #333d4b;
  --color-paleOrange: #fdd6ba;
  --color-lightCream: #fefcf7;
  --color-grey: #83888f;
  --color-primary-text: #fefcf7;
  --color-second-text: #333d4b;
  --color-third-text: #83888f;
  --color-bg-primary: #fefcf7;
  --color-bg-second: #2c343e;
  --lh-xl: 0.48;
  --lh-l: 1;
  --lh-m: 1.2;
  --lh-s: 1.12;
  --lh-xs: 1.33;
  --lh-body-m: 1.62;
  --lh-body-s: 1.25;
  --lh-button: 1.39;
  --fw-regular: 400;
  --fw-bold: 700;
  --fw-black: 900;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* better for people who browse with the keyboard and know where they are on the site, so I set a different style, making it more visible, taking it from the default.
 */
*:focus-visible {
  outline: #646464 dotted 4px;
  outline-offset: 4px;
}

ul {
  list-style: none;
}

a:link {
  text-decoration: none;
}

img {
  display: block;
}

select {
  border: none;
  background: none;
}

@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    background-attachment: initial !important;
    transition-delay: 0s !important;
    transition-duration: 0s !important;
    -webkit-animation-duration: 1ms !important;
    animation-duration: 1ms !important;
    -webkit-animation-delay: -1ms !important;
    animation-delay: -1ms !important;
    -webkit-animation-iteration-count: 1 !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
  }
}
/**
 * Screen Reader Class
 * 
 * To hide content visually.
 *
 * For more information: https://gist.github.com/ffoodd/000b59f431e3e64e4ce1a24d5bb36034
 */
.sr-only {
  position: absolute !important;
  margin: -1px !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  white-space: nowrap !important;
}

html {
  overflow-x: hidden;
}

body {
  transition: 0.4s ease-in-out;
  background: var(--color-bg);
  line-height: 1.5;
  /* improves text rendering */
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
  position: relative;
}

body.dark-theme {
  transition: 0.4s ease-in-out;
  --color-second-text: #fefcf7;
  --color-third-text: #fefcf7;
  --color-bg: #333d4b;
  --color-bg-second: #2c343e;
}
body.dark-theme .header__logo g path:first-child {
  fill: #fefcf7;
}
body.dark-theme .works__details .circle-1,
body.dark-theme .works__details .circle-2,
body.dark-theme .works__details .circle-3 {
  background: transparent;
}
body.dark-theme #theme-button {
  color: var(--color-third-text);
}
body.dark-theme .headquarters__title {
  color: var(--color-primary-text);
}
body.dark-theme .btn-box .btn--padding {
  color: var(--color-darkGreyBlue);
  opacity: 0.4;
}
body.dark-theme .btn-box .btn--padding.activeButton {
  opacity: 1;
  color: var(--color-primary-text);
}

.btn:link,
.btn:visited {
  display: inline-block;
  font: var(--fw-black) 1.125rem/var(--lh-button) "Fraunces", serif;
  color: var(--color-lightCream);
  padding: 0.9375rem 5.0625rem 1rem 5.0625rem;
  background-color: var(--color-darkCyan);
  border-radius: 0.375rem;
  transition: 0.3s;
}
.btn:link:hover,
.btn:visited:hover {
  background-color: #66d2cf;
}

.btn-disable {
  background-color: #e2dedb !important;
}

/* INDEX */ /* INDEX */
input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 52px;
  height: 27px;
  background: grey;
  float: right;
  border-radius: 100px;
  position: relative;
}

label:after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s;
}

input:checked + label {
  background: var(--color-darkCyan);
}

input:checked + label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

label:active:after {
  width: 45px;
}

