/* PLAN */
.plan {
  max-width: 74.375rem;
  padding: 0 2.5rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 7.8125rem;
  margin-bottom: 10.5rem;
  justify-items: end;
}
@media (max-width: 65.625rem) {
  .plan {
    grid-template-columns: 1fr;
    gap: 5rem;
    justify-items: center;
  }
}
@media (max-width: 55rem) {
  .plan {
    margin-bottom: 9rem;
  }
}
@media (max-width: 41.25rem) {
  .plan {
    margin-bottom: 7.5rem;
  }
}
.plan__options {
  max-width: 15.9375rem;
}
@media (max-width: 65.625rem) {
  .plan__options {
    display: none;
  }
}
.plan__options .plan__list {
  display: grid;
  gap: 1.5rem;
}
.plan__options .plan__list .plan__item {
  font: var(--fw-black) 1.5rem/var(--lh-xs) "Fraunces", serif;
  color: var(--color-darkGreyBlue);
}
@media (max-width: 65.625rem) {
  .plan__options .plan__list .plan__item {
    font-size: 1.25rem;
    line-height: 1;
  }
}
.plan__options .plan__list .plan__item:nth-child(4).block .plan__item--content {
  position: relative;
}
.plan__options .plan__list .plan__item:nth-child(4).block .plan__item--content .plan__item--number,
.plan__options .plan__list .plan__item:nth-child(4).block .plan__item--content .plan__item--title {
  opacity: 0.2;
}
.plan__options .plan__list .plan__item:nth-child(4).block .plan__item--content [data-disabled=blockGrind] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0.4;
  filter: alpha(opacity=50);
  cursor: no-drop;
  display: none;
}
.plan__options .plan__list .plan__item:nth-child(4).block .plan__item--content [data-disabled=blockGrind].block {
  display: block;
}
.plan__options .plan__list .plan__item--content {
  display: flex;
  gap: 1.8125rem;
  cursor: pointer;
  opacity: 0.4;
  transition: 0.4s ease-in-out;
}
.plan__options .plan__list .plan__item--content:hover {
  opacity: 0.6;
}
.plan__options .plan__list .plan__item--content.active {
  opacity: 1;
}
.plan__options .plan__list .plan__item--content .plan__item--number {
  color: var(--color-grey);
}
.plan__options .plan__list .plan__item--content .plan__item--number--green {
  color: var(--color-darkCyan);
}
.plan__options .plan__list .plan__item--content .plan__item--number.active {
  opacity: 1;
}
.plan__options .plan__list .plan__item--content .plan__item--title {
  transition: 0.4s;
}
body.dark-theme .plan__options .plan__list .plan__item--content .plan__item--title {
  color: var(--color-primary-text);
}
.plan__options .plan__list .plan__item--content .plan__item--title.active {
  opacity: 1;
}
.plan__options .plan__list .plan__item::after {
  content: "";
  display: block;
  width: 15.9375rem;
  height: 0.0625rem;
  background-color: var(--color-grey);
  margin-top: 1.5rem;
  opacity: 0.25;
}
.plan__selection {
  max-width: 45.625rem;
  display: grid;
}
.plan__selection .plan__preferences {
  display: grid;
  gap: 3.5rem;
  margin-bottom: 5.5rem;
  position: relative;
}
.plan__selection .plan__preferences [data-disabled=block] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 0.4;
  filter: alpha(opacity=50);
  cursor: no-drop;
  display: none;
}
.plan__selection .plan__preferences [data-disabled=block].block {
  display: block;
}
@media (max-width: 55rem) {
  .plan__selection .plan__preferences {
    margin-bottom: 6.25rem;
  }
  .plan__selection .plan__preferences:nth-child(5) {
    margin-bottom: 9rem;
  }
}
@media (max-width: 41.25rem) {
  .plan__selection .plan__preferences {
    gap: 2rem;
    margin-bottom: 6.875rem;
  }
  .plan__selection .plan__preferences:nth-child(5) {
    margin-bottom: 7.5rem;
  }
}
.plan__selection .plan__preferences .plan__title {
  font: var(--fw-black) 2.5rem/var(--lh-m) "Fraunces", serif;
  color: var(--color-darkGreyBlue);
  border: none;
  background: none;
  color: var(--color-grey);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
@media (max-width: 73.125rem) {
  .plan__selection .plan__preferences .plan__title {
    font-size: 2rem;
    line-height: 1.5;
  }
}
@media (max-width: 41.25rem) {
  .plan__selection .plan__preferences .plan__title {
    font-size: 1.5rem;
    line-height: 1.17;
  }
}
.plan__selection .plan__preferences .plan__title::after {
  content: "";
  display: block;
  width: 1.1875rem;
  height: 0.8125rem;
  background: url(../img/plan/desktop/icon-arrow.svg) no-repeat;
}
.plan__selection .plan__preferences .plan__title.active::after {
  transform: rotate(180deg);
}
.plan__selection .plan__preferences .plan__content {
  display: flex;
  gap: 1.4375rem;
  display: none;
}
.plan__selection .plan__preferences .plan__content button {
  border: none;
  background: none;
}
@media (max-width: 55rem) {
  .plan__selection .plan__preferences .plan__content {
    gap: 0.625rem;
  }
}
@media (max-width: 41.25rem) {
