}
.plan__selection .plan__preferences .plan__content .plan__reply--title {
  font: var(--fw-black) 1.5rem/var(--lh-xs) "Fraunces", serif;
  color: var(--color-darkGreyBlue);
  color: var(--color-darkGreyBlue);
  margin-bottom: 1.5rem;
}
.plan__selection .plan__preferences .plan__content .plan__reply--desc {
  font: var(--fw-regular) 1rem/var(--lh-body-m) "Barlow", sans-serif;
  color: var(--color-lightCream);
  opacity: 0.8;
  color: var(--color-darkGreyBlue);
}
.plan .btn-box {
  display: inline-block;
  position: relative;
}
.plan .btn-box [data-disabled=blockGrind] {
  position: absolute;
