.modal {
  max-width: 33.75rem;
  border-radius: 0.5rem;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: none;
  z-index: 1;
}
.modal.active {
  display: block;
}
.modal .modal__title-box {
  background: var(--color-darkGreyBlue);
  padding: 3rem 0 2.5rem 3.5rem;
  border-radius: 0.5rem 0.5rem 0 0;
}
.modal .modal__title-box .modal__title {
  font: var(--fw-black) 2.5rem/1.2 "Fraunces", serif;
  color: #fff;
}
.modal__content {
  background: var(--color-lightCream);
  padding: 3.5rem;
  border-radius: 0 0 0.5rem 0.5rem;
}
.modal__content .plan__order-summary--modal {
  color: #83888f;
  margin-bottom: 0.4375rem;
}
.modal__content .plan__order-summary--modal [data-modal=modal--complet].activeColor {
  color: #83888f !important;
}
.modal__content .modal__desc {
  font: var(--fw-regular) 1rem/var(--lh-body-m) "Barlow", sans-serif;
  color: var(--color-lightCream);
  opacity: 0.8;
  color: var(--color-darkGreyBlue);
  margin-bottom: 2.9375rem;
}
.modal__content .btn-modal {
  padding: 0.9375rem 4rem 0.9375rem 4rem;
}
.modal__content .modal__checkout {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.4375rem;
}
.modal__content .modal__checkout .modal__price {
  font: var(--fw-black) 2rem/var(--lh-s) "Fraunces", serif;
  color: var(--color-darkGreyBlue);
}

.overlay {
  width: 100%;
  height: 100%;
  background: #000;
  opacity: 0.5;
  position: absolute;
  top: 0;
  display: none;
}
.overlay.active {
  display: block;
}

.section {
  transition: transform 1s, opacity 1s;
}

.header.sticky {
  position: fixed;
  top: 0;
  left: 0 !important;
  right: 0;
  background-color: rgba(255, 255, 255, 0.95);
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.4);
  animation: show-up 1s forwards;
  z-index: 100;
}

@keyframes show-up {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
.section--hidden {
  opacity: 0;
  transform: translateY(8rem);
}

